// src/components/ProtectedRoute.js
import { Navigate, Outlet } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth'; // adjust path if your hook lives elsewhere

export default function ProtectedRoute({ redirectTo = '/login', roles }) {
  const { user, loading } = useAuth();

  if (loading) {
    // Show a centered spinner while checking auth
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-green-500"></div>
      </div>
    );
  }

  if (!user) {
    return <Navigate to={redirectTo} replace />;
  }

  if (roles && !roles.some(r => user.roles?.includes(r))) {
    return <Navigate to="/403" replace />;
  }

  return <Outlet />; // will render the child routes defined in App.js
}
